  align-items: center;
  justify-content: space-between;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 15px;
  color: #19385E;
  height: 60px;
  position: relative;
  z-index: 1;
}

.header_mobile__menu-header::after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: -15px;
  right: -15px;
  z-index: -1;
  background-color: #E8F3F8;
}

.header_mobile__menu-header:hover,
.header_mobile__menu-header:active,
.header_mobile__menu-header:focus {
  text-decoration: none;
}

.header_mobile__menu-content {
  padding: 0 0 30px 0;
}

.header_mobile__menu-list {
  margin: 0 0 25px 0;
}

.header_mobile__menu-list > li {
  position: relative;
}

.header_mobile__menu-list > li::after {
  content: "";
  display: block;
  position: absolute;
  left: -15px;
  right: -15px;
  height: 1px;
  background: #E8F3F8;
}

.header_mobile__menu-list > li > a {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 60px;
  font-weight: 600;
  font-size: 15px;
  line-height: 1.1;
  text-transform: uppercase;
}

.header_mobile__menu-list > li > a:hover,
.header_mobile__menu-list > li > a:active,
.header_mobile__menu-list > li > a:focus {
  text-decoration: none;
}

.header_mobile__menu-list > li > ul {
  display: none;
  margin: 0 0 15px 0;
}

.header_mobile__menu-list > li > ul > li {
  display: block;
}

.header_mobile__menu-list > li > ul > li:not(:last-child) {
  margin: 0 0 6px 0;
}

.header_mobile__menu-list > li.active > a img {
  transform: rotate(-180deg);
}

.header_mobile__menu-list > li.active > ul {
  display: block;
}

.header_mobile__menu-locations {
  margin: 0 0 35px 0;
}

.header_mobile__menu-locations > li {
  font-size: 14px;
}

.header_mobile__menu-locations > li:not(:last-child) {
  margin: 0 0 14px 0;
}

.header_mobile__menu-locations > li a {
  position: relative;
  display: inline-flex;
  padding: 0 0 0 20px;
  background: url("../images/icons/pin.svg") left 4px no-repeat;
  text-decoration: underline;
}

.header_mobile__menu-locations > li a:hover,
.header_mobile__menu-locations > li a:active,
.header_mobile__menu-locations > li a:focus {
  text-decoration: none;
}

